<template>
  <div>
    <el-badge :value="value" class="item">
      <el-button size="small">评论</el-button>
    </el-badge>
    <el-button icon="el-icon-remove-outline" @click="divi"></el-button>
    <el-button icon="el-icon-circle-plus-outline" @click="add"></el-button>
  </div>

</template>

<script>
export default {
  name: "Layout",
  data(){
    return {
      value:0
    }
  },
  methods:{
    add(){
      if (this.value<10)
        this.value++
    },
    divi(){
      if (this.value>0)
        this.value--
    }
  }
}
</script>

<style scoped>

</style>



